






















































import uniSegmentedControl from '../../../components/uni-segmented-control.vue';

export default {
	data() {
		return {
			items: [
				'大循环',
				'文理学部',
				'工学部'
			],
			styles: [{
				value: 'button',
				text: '按钮'
			}, {
				value: 'text',
				text: '文字'
			}],
			colors: [
				'#4cd964',
				'#007aff',
				
				'#dd524d'
			],
			current: 0,
			activeColor: '#4cd964',
			styleType: 'button',
			styleIndex: 0,
			colorIndex: 0,
		}
	},
	components: {
		uniSegmentedControl
	},
	methods: {
		save(){
			uni.showActionSheet({
				itemList:['保存图片到相册'],
				success: () => {
					plus.gallery.save('https://img.cdn.aliyun.dcloud.net.cn/guide/uniapp/app_download.png', function() {
						uni.showToast({
							title:'保存成功',
							icon:'none'
						})
					}, function() {
						uni.showToast({
							title:'保存失败，请重试！',
							icon:'none'
						})
					});
				}
			})
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		styleChange(evt) {
			const value = evt.target.value;
			if (this.styleType !== value) {
				this.styleType = value;
				for (let i = 0; i < this.styles.length; i++) {
					if (this.styles[i].value === value) {
						this.styleIndex = i;
						break;
					}
				}
			}
		},
		colorChange(evt) {
			if (this.styleType !== evt.target.value) {
				this.activeColor = evt.target.value;
				this.colorIndex = this.colors.indexOf(evt.target.value);
			}
		}
	}
}
